import { Typography } from '@material-ui/core';
import { makeStyles} from '@material-ui/core/styles';

const useStyles = makeStyles ({

    contentTextStyle: {
      textAlign: "center",
      color: "black",
      padding: '10px',
      margin: '30px',
  
    },

    welcomeTextStyle: {
        textAlign: "center",
        color: "black",
        padding: '100px 0px 10px 0px',
        margin: '30px',
    }

  });

function Welcome() {
  
    const styles = useStyles();
  
    return (

        <div>
            <Typography
                variant="h4"
                className={styles.welcomeTextStyle}
            >
                Welcome to my portfolio!
            </Typography>
            <Typography
                variant="body1"
                className={styles.contentTextStyle}
            >
                This is a portfolio page where I aim to display the projects I am most proud of!
            </Typography>
        </div>

    );
  }
  
  export default Welcome;