import MyPic from "./johannapic.jpg";
import { Typography, IconButton } from '@material-ui/core';
import { makeStyles} from '@material-ui/core/styles';
import FacebookIcon from '@material-ui/icons/Facebook';
import InstagramIcon from '@material-ui/icons/Instagram';

const useStyles = makeStyles ({
  
    imageStyle: {
      width: "300px",
      height: "400px",
      border: "2px solid white",
      float: "left",
    },

    aboutText: {
        fontSize: "20px",
        lineHeight: "2",
        margin: "60px 0px 20px 20px",
    },

    iconStyle: {
        fontSize: "60px",
    },

    findMeText: {
        fontSize: "20px",
        margin: "50px 0px 20px 20px",
    },

    flexContainer: {
        display: "flex",
        justifyContent: "flex-end",
    },

    imageContainer: {
        flexGrow: 1,
        display: "flex",
        justifyContent: "flex-end",
        margin: "30px 20px 0px 0px",
    },

    infoContainer: {
        flexGrow: 1,
    },

  });


function AboutMe() {
  
    const styles = useStyles();

    return (
        
            <div
                className={styles.flexContainer}
            >
                <div
                    className={styles.imageContainer}
                >
                    <img  
                        src={MyPic}
                        className={styles.imageStyle}
                        alt="A picture of me"
                    />
                </div>
                <div
                    className={styles.infoContainer}
                >
                    <Typography
                        className={styles.aboutText}
                    >
                        Name: Johanna Pakarinen <br/>
                        Age: 29 <br/>
                        From: Tampere, Finland <br/>
                        Hobbies: Gaming, writing, drawing, baking
                    </Typography>


                    <div>
                        <div>
                            <Typography
                                className={styles.findMeText}
                            >
                                Find me at:
                            </Typography>
                        </div>
                        <div>
                            <IconButton
                                onClick={ () => {
                                    window.open("https://www.facebook.com/sakumartti/", "_blank")
                                }}
                            >
                                <FacebookIcon
                                    className={styles.iconStyle}
                                />
                            </IconButton>
                            <IconButton
                                onClick={ () => {
                                    window.open("https://www.instagram.com/zetsunida/", "_blank")
                                }}
                            >
                                <InstagramIcon
                                    className={styles.iconStyle}
                                />
                            </IconButton>
                        </div>
                    </div>
                </div>
            </div>

    );
  }
  
  export default AboutMe;