import { Typography, Card, CardActions, CardContent, CardMedia, Button, Dialog, DialogContent, DialogActions } from '@material-ui/core';
import { makeStyles} from '@material-ui/core/styles';
import { useState } from 'react';

const useStyles = makeStyles ({

    cardImage: {
        height: "300px",
    },

    cardBase: {
        width: "400px",
        margin: "20px",
        height: "520px",
    },

    cardTitleText: {
        fontSize: "20px",
        textAlign: "center",
        marginBottom: "15px",
    },

    cardContentText: {
        fontSize: "14px",
        textAlign: "justify",
        margin: "20px 10px 5px 10px",
    },

    buttonWrap: {
        display: "flex",
        justifyContent: "center",
    },

    buttonStyle: {
        color: "#166e82",
    },

    imageStyle: {
        maxWidth: "1500px",
        maxHeight: "1000px",
    },


  });

function CardDialog(props) {
  
    const styles = useStyles();

    //Tilamuuttuja, jossa käsitellään dialogin avausta.
    const [dialogOpen, setDialogOpen] = useState(false);

    const handleClickOpen = () => {
        setDialogOpen(true);
    };
    const handleClose = () => {
        setDialogOpen(false);
    };
  
    return (

        <div>
        <Card
            className={styles.cardBase}
        >
                <CardMedia 
                    className={styles.cardImage}
                    image={props.projectCard.image}
                    title={props.projectCard.imageTitle}
                />
                <CardContent>
                    <Typography
                    className={styles.cardTitleText}
                    >
                        {props.projectCard.title}
                    </Typography>
                    <Typography
                        className={styles.cardContentText}
                    >
                        {props.projectCard.description}
                    </Typography>
                </CardContent>
            <CardActions
                className={styles.buttonWrap}
            >
                <div>
                    <Button
                        className={styles.buttonStyle}
                        onClick={handleClickOpen}
                    >
                        View image
                    </Button>
                </div>
            </CardActions>
        </Card>
        <Dialog
            onClose={handleClose}
            open={dialogOpen}
            maxWidth="xl"
        >
            <DialogContent>
                <img
                    src={props.projectCard.image} 
                    className={styles.imageStyle}
                />
            </DialogContent>
            <DialogActions>
                <Button
                    onClick={handleClose}
                    className={styles.buttonStyle}
                >
                    Close
                </Button>
            </DialogActions>
        </Dialog> 
    </div>

    );
  }
  
  export default CardDialog;