import { useState, useEffect} from 'react';
import { Paper, Tabs, Tab, CssBaseline } from '@material-ui/core';
import { makeStyles} from '@material-ui/core/styles';
import { useHistory } from 'react-router-dom';

const useStyles = makeStyles ({
  
    navBarStyle: {
      flexGrow: 1,
      background: "white",
    },
  
  });


function Navbar(props) {
  
    const styles = useStyles();
    const history = useHistory();

    const [value, setValue] = useState(0);
    const handleChange = (event, newValue) => {
      
    setValue(newValue);

    //If-lauseella katsotaan mille näkymälle mennään valuen perusteella.
      if (newValue === 0) {
        history.push('/');
      } 
      else if (newValue === 1) {
        history.push('/aboutme');
      }
      else if (newValue === 2) {
        history.push('/projects');
      }
    }

    //"Kuuntelee" historiaa ja päivittää navigointipalkin seuraamaan oikeaan näkymään, mikäli menee taaksepäin nuolilla selaimessa.
    useEffect(() => {
        return history.listen((location) => { 
           if (location.pathname === '/') {
               setValue(0);
           }
           else if (location.pathname === '/aboutme') {
            setValue(1);
           }
           else if (location.pathname === '/projects') {
                setValue(2);
           }
        }) 
    },[history])
  
    return (
        
        <div
            className={styles.navBarStyle}
        >
            <CssBaseline />

                <Tabs
                    value={value}
                    onChange={handleChange}
                    indicatorColor="primary"
                    textColor="primary"
                    centered
                >

                    <Tab 
                        value={0}
                        label="Home" 
                    />
                    <Tab 
                        value={1}
                        label="About Me"
                    />
                    <Tab 
                        value={2}
                        label="My Projects" 
                    />

                </Tabs>

        </div>

    );
  }
  
  export default Navbar;