import { makeStyles} from '@material-ui/core/styles';
import islaverano from "./islaverano.jpg";
import dodugoose from "./dodugoose.png";
import selene from "./selene.png";
import { useState } from 'react';
import CardDialog from './carddialog';


const useStyles = makeStyles ({

    flexContainer: {
        display: "flex",
        justifyContent: "center",
        overflow: "auto",
        flexWrap: "wrap",
    },

});

function Projects() {
  
    const styles = useStyles();
    //Cards-array, jossa on objektin attribuutteina kaikki mitä Card tarvitsee.
    const projectCards = [
        {
            title: "HTML & CSS // My Animal Crossing Page",
            description: "This is a website I made for my Animal Crossing New Horizons island!",
            imageTitle: "Isla Verano's Website",
            image: islaverano,
        },
        {
            title: "Art // Dodugoose",
            description: "A Pokémon combination of a doduo and a zangoose 19.05.2018",
            imageTitle: "Doduo and Zangoose in one",
            image: dodugoose,
        },
        {
            title: "Art // Selene",
            description: "An original character called Selene 04.05.2018",
            imageTitle: "Selene",
            image: selene,
        },
        
    ];
  
    return (
    
        <div
            className={styles.flexContainer}
        >
            {/* Loin Cards-komponentit arrayn avulla (jä nämä on erikseen tehty vielä CardDialogissa), jolloin minun ei tarvinnut
                kirjoittaa jokaiselle Card:ille omia tietoja. */}
            {projectCards.map( (projectCard, idx) => {
                return (
                 <CardDialog key={idx} projectCard={projectCard}></CardDialog>
                );
            })}
            
        </div>

    );
  }
  
  export default Projects;